.card {
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  margin: 1rem;
  width: 14rem;
  height: 14rem;
  cursor: pointer;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
.wrapper {
  padding-top: 50px;
  background: rgb(223, 221, 124);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: #2A77AE;
  margin-bottom: 50px;
}

.navbarText {
    margin-left: 42%;
    font-weight: bold;
    font-size: 30px;
    color: white!important; 
    padding-top: 1%;
    padding-bottom: 1%;
}
.footerText {
    margin-left: 42%;
  
}

.footer {
    height: 40px;
}

.footerContainer {
    padding-top: 10px !important; 
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

